import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { About1Component } from './about1/about1.component';
import { SdlcaboutComponent } from './sdlcabout/sdlcabout.component';
import {RouterModule,Routes} from '@angular/router';
import { HomedescComponent } from './homedesc/homedesc.component';
import { AboutKUComponent } from './about-ku/about-ku.component';
import { AboutWarangalComponent } from './about-warangal/about-warangal.component';
const routing:Routes=[
  {path:'',component:About1Component,
children:[
  {path:'aboutsdlc',component:SdlcaboutComponent},
  {path:'description',component:HomedescComponent},
  {path:'about-ku',component:AboutKUComponent},
  {path:'about-warangal',component:AboutWarangalComponent},
  
]}
]
@NgModule({
  declarations: [About1Component, SdlcaboutComponent, HomedescComponent, AboutKUComponent, AboutWarangalComponent],
  imports: [
    CommonModule,RouterModule.forChild(routing)
  ]
})
export class AboutModule { }
